//@blue: #1a7af8;

.title-container {
    height: 52px;
    width: 100%;
    background: #1a7af8;
    z-index: 50;
    top: 0;
    position: fixed;
    .web-logo {
        margin-left: 50px;
        line-height: 52px;
        font-size: 30px;
        color: honeydew;
        display: flex;
        //justify-content: center;
        align-items: center;
        img {
            width: 30px;
            height: 30px;
            margin-left: 10px;
        }
    }
}

.footer {
    height: 60px;
    width: 100%;
    background: #1a7af8;
    z-index: 50;
    .copyright {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        a {
            color: white;
        }
    }
}

.qtable-container {
    margin: 80px 50px 0;
    min-height: 100vh;
    .handle-item {
        height: calc(100% - 24px);
        display: flex;
        align-items: center;
    }
}

